import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap'
import { getTransactionName, getShareHost } from '../js/gegeUtil.js'
import AccountDialog from '../../conponents/AccountDialog'
import BlockDialog from '../../conponents/BlockDialog/BlockDialog'
const get = require('simple-get')
const WebTorrent = require('gege-torrent')
const moment = require('moment');
class BlockList extends Component {

  constructor(props) {
    super(props);
    this.page = 1;
    this.state = {
      accountInfo: null, list: [], dropdownOpen: new Array(2).fill(false), filterOption: '',
      filterIdx: 0, filterValue: '', page: 1, danger: false, showAccount: false, blockInfo: null
    }
    this.filterOptions = ['All(全部)', 'Total Fee(手续费)', 'Total Amount(金额)', 'Miner(矿工)', 'Height(高度)']
    this.filterMsg = ['', 'Type Total Fee(输入总手续费)', 'Type Total Amount(输入交易总金额)', 'Type Miner account(输入矿工账号)', 'Type height(輸入區塊高度)']
  }

  componentDidMount() {
    this.loadLoadBlockList(1, this.state.filterIdx)
  }

  loadLoadBlockList(page, filterIdx, filterValue) {
    let url =getShareHost() + '/btb?action=blockList&page=' + page + "&filterIdx=" + filterIdx + "&filterValue=" + filterValue;

    get.concat(url, (function (err, res, data) {
      if (err) {
        //console.log(err);
        return;
      }
      try {
        let list = JSON.parse(data);
        this.setState({ list })
      } catch (e) {
      }
    }).bind(this))
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      this.loadLoadBlockList(this.state.page, this.state.filterIdx, this.state.filterValue)
    }
  }

  toggleDanger() {
    this.setState({
      danger: !this.state.danger,
    });
  }

  loadBlockInfo(height) {
    this.toggleDanger()
    let url =getShareHost() + '/btb?action=block&height=' + height;
    get.concat(url, (function (err, res, data) {
      if (err) {
        // console.log(err);
        return;
      }
      let blockInfo = JSON.parse(data);
      if (blockInfo.ok)
        this.setState({ blockInfo })
    }).bind(this))
  }

  nextPage() {
    this.state.page++
    this.loadLoadBlockList(this.state.page, this.state.filterIdx, this.state.filterValue)
  }

  filterBlocks() {
    this.loadLoadBlockList(this.state.page, this.state.filterIdx, this.state.filterValue)
  }

  renderBlockList() {
    let list = this.state.list;
    if (!list)
      return null;
    let content = list.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><BlockDialog height={res.height} /></td>
        <td><AccountDialog rsId={res.generatorId} /></td>
        <td>{res.amount} BTB</td>
        <td>{res.fee} BTB</td>
        <td>{res.difficulty}</td>
        <td>{btime}</td>
      </tr>)
    });

    return content;
  }

  renderAccountTransactionItem() {
    let content = this.state.accountInfo.transactions.map((res) => {
      return (<tr>
        <td>{getTransactionName(res.type)}</td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} BTB</td>
        <td>{res.fee} BTB</td>
        <td>{res.height}</td>
      </tr>)
    });

    return content;
  }

  renderAccountTransactions() {
    if (this.state.accountInfo.transactions) {
      return (<Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>Type(交易类型)</th>
            <th>From(发起账户)</th>
            <th>To(接收账户)</th>
            <th>Amount(金额)</th>
            <th>Fee(手续费)</th>
            <th>Height(区块高度)</th>
          </tr>
        </thead>
        <tbody>
          {this.renderAccountTransactionItem()}
        </tbody>
      </Table>)
    } else {
      return null;
    }
  }

  rederAccountInfoDialog() {
    if (!this.state.accountInfo)
      return null;
    return (<Modal isOpen={this.state.showAccount} toggle={this.toggleAccount.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleAccount.bind(this)}>Account Info.(账户明细)</ModalHeader>
      <ModalBody>
        <p> Account ID(账户ID): {this.state.accountInfo.rsId}</p>
        <p> Account PK(账户公钥): {this.state.accountInfo.pk}</p>
        <p> Account Balance(账户余额): {this.state.accountInfo.balance}</p>
        <p>Transactions(交易列表):</p>
        {this.renderAccountTransactions()}
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleAccount.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  toggleAccount() {
    this.setState({
      showAccount: !this.state.showAccount,
    });
  }

  showAccountInfo(rsId) {
    this.toggleAccount();
    let url =getShareHost() + '/btb?action=account&rsIdOrPkHex=' + rsId;
    get.concat(url, (function (err, res, data) {
      if (err) {
        // console.log(err);
        return;
      }
      let accountInfo = JSON.parse(data);
      this.setState({ accountInfo })
    }).bind(this))
  }


  toggle(i) {
    const newArray = this.state.dropdownOpen.map((element, index) => { return (index === i ? !element : false); });
    this.setState({
      dropdownOpen: newArray,
    });
  }

  setFilter(idx) {
    this.setState({ page: 1, filterIdx: idx, filterOption: this.filterMsg[idx] })
  }


  upateFilterValue(e) {
    if (e && e.target) {
      let value = e.target.value;
      this.setState({ filterValue: value })
    }
  }

  renderFilterOptions() {
    let ops = this.filterOptions.map((op, index) => {
      return (<DropdownItem onClick={(e) => {
        this.setFilter(index)
      }}>{op}</DropdownItem>)
    })
    return ops;
  }

  rederBlockInfoDialog() {
    if (!this.state.blockInfo)
      return null;
    return (<Modal isOpen={this.state.danger} toggle={this.toggleDanger.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDanger.bind(this)}>Block Info.(区块明细)</ModalHeader>
      <ModalBody>
        <p> Block ID(区块ID): {this.state.blockInfo.block.id}</p>
        <p> Block Height(区块高度): {this.state.blockInfo.block.height}</p>
        <p>Transactions(交易列表):</p>
        {this.renderBlockTransactions()}
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDanger.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderList() {
    let list = this.state.list;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <ButtonDropdown isOpen={this.state.dropdownOpen[1]} toggle={() => { this.toggle(1); }}>
                <DropdownToggle caret>
                  Filter(过滤)
                    </DropdownToggle>
                <DropdownMenu>
                  {this.renderFilterOptions()}
                </DropdownMenu>
              </ButtonDropdown>
              <Input placeholder={this.state.filterOption} onChange={this.upateFilterValue.bind(this)} />
              <Button color="primary" onClick={(e) => this.filterBlocks()}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            {this.rederBlockInfoDialog()}
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Height(区块高度)</th>
                  <th>Miner(矿工)</th>
                  <th>Amount(金额)</th>
                  <th>Fee(手续费)</th>
                  <th>Difficulty(难度)</th>
                  <th>Time(时间)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderBlockList()}
                {this.rederAccountInfoDialog()}
              </tbody>
            </Table>
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev(上一页)</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next(下一页)</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  renderTransactionItem() {
    let content = this.state.blockInfo.transactions.map((res) => {
      return (<tr>
        <td>{getTransactionName(res.type)}</td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} BTB</td>
        <td>{res.fee} BTB</td>
        <td>{res.height}</td>
      </tr>)
    });

    return content;
  }

  renderBlockTransactions() {
    if (this.state.blockInfo.transactions) {
      return (<Table hover bordered striped responsive size="sm">
        <thead>
          <tr>
            <th>Type(交易类型)</th>
            <th>From(发起账户)</th>
            <th>To(接收账户)</th>
            <th>Amount(金额)</th>
            <th>Fee(手续费)</th>
            <th>Height(区块高度)</th>
          </tr>
        </thead>
        <tbody>
          {this.renderTransactionItem()}
        </tbody>
      </Table>)
    } else {
      return null;
    }
  }


  render() {
    return (
      <div className="animated fadeIn">
        {this.renderList()}
      </div>

    );
  }
}

export default BlockList;



